<template>
  <div>
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="150px" class="demo-ruleForm">
      <el-form-item class="el-self-item_class" label="" prop="name">
        <input v-model="ruleForm.name" placeholder="" auto-complete="off" class="spec-input"/>
        <span style="color: #e33c32">*</span>
      </el-form-item>
      <el-form-item class="el-self-item_class" label="" prop="mobile">
        <input v-model="ruleForm.mobile" placeholder="" auto-complete="off" class="spec-input"/>
        <span style="color: #e33c32">*</span>
        <span v-show="sendAuthCode" class="auth_text_btn auth_text_blue" @click="getAuthCode"></span>
        <span v-show="!sendAuthCode"><span class="auth_text_blue">{{auth_time}}</span></span>
      </el-form-item>
      <el-form-item class="el-self-item_class" label="" prop="veriCode">
        <input v-model="ruleForm.veriCode" placeholder="" class="spec-input"/>
        <span style="color: #e33c32">*</span>
      </el-form-item>
      <el-form-item class="el-self-item_class" label="" prop="newPassword">
        <input v-model="ruleForm.newPassword" placeholder="" class="spec-input"/>
        <span style="color: #e33c32">*</span>
      </el-form-item>
      <el-form-item class="el-self-item_class">
        <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        <el-button @click="resetForm('ruleForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  data () {
    var validateVeriCode = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error(''))
      } else {
        callback()
      }
    }
    var validateName = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    var validateMobile = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    var validateNewPassWord = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (value.length < 6 || value.length < 20) {
        callback(new Error('6-20'))
      } else {
        callback()
      }
    }
    return {
      // v-show
      sendAuthCode: true,
      //  
      auth_time: 0,
      ruleForm: {
        name: '',
        mobile: '',
        veriCode: '',
        newPassword: ''
      },
      rules: {
        name: [
          { validator: validateName, trigger: 'blur' }
        ],
        mobile: [
          { validator: validateMobile, trigger: 'blur' }
        ],
        veriCode: [
          { validator: validateVeriCode, trigger: 'blur' }
        ],
        newPassword: [
          { validator: validateNewPassWord, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    update () {
      this.resetForm('ruleForm')
    },
    getAuthCode: function () {
      if (!(/^1[34578]\d{9}$/.test(this.ruleForm.mobile))) {
        this.$message.error('')
        return
      }
      this.$store.dispatch('queryVeriCode', {mobile: this.ruleForm.mobile, type: '1'}).then((response) => {
        this.sendAuthCode = false
        this.auth_time = 60
        var timer = setInterval(() => {
          this.auth_time--
          if (this.auth_time <= 0) {
            this.sendAuthCode = true
            clearInterval(timer)
          }
        }, 1000)
      }).catch(() => {
      })
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$store.dispatch('queryFindPwd', this.ruleForm).then((response) => {
            if (response.RetCode === 1) {
              this.$message({
                type: 'success',
                message: ''
              })
              this.$emit('close')
            } else {
              this.$message.error(response.Msg)
            }
          }).catch(() => {
          })
        } else {
          // console.log('error submit!!')
          return false
        }
      })
    },
    resetForm (formName) {
      this.sendAuthCode = true
      this.$refs[formName].resetFields()
    }
  }
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
// .demo-ruleForm{
//   background: #fff;
// }
// .el-self-item_class{
//   background: #fff;
// }
.login-container .el-form-item{
  background: #fff;
}
// .el-input{
//   width: 175px;
//   background: white;
// }
.spec-input{
  border: 1px solid #e0e0e0;
  width: 255px;
  height: 34px;
  line-height: 34px;
  border-radius: 5px;
  padding: 0 10px;
  color: #5e5e5e;
}
.auth_text_blue{
  color: #37bfc8;
}
.auth_text_btn{
  cursor: pointer;
}
</style>
<style scoped>
/* .spec-input >>> input {
  border: 1px;
  border-color: black;
} */
</style>



